.form {
  display: flex;
  flex-direction: column;

  &__item {
    margin-bottom: 16px;
  }

  &__input {
    border: none;
    height: 48px;
  }

  &__textarea {
    height: 144px;
    font-family: "Roboto", sans-serif;
    font-size: 14px;
  }

  &__button,
  &__input {
    width: 100%;
  }

  &__button {
    margin-top: 16px;
    background-color: #fff;
    color: $heightlight-color;
    transition: background-color 0.3s, color 0.3s;
    &:hover {
      cursor: url("../images/Cursor.svg");
      background-color: $heightlight-color;
      color: #fff;
      box-shadow: 0 6px 23px rgba(0, 117, 255, 0.46);
    }
  }
}
